@import url(https://fonts.googleapis.com/css?family=Nunito);
html {
  height: 100%;
  font-size: 62.5%; }

body {
  height: 100%;
  background-color: rgba(31, 200, 219, 0.2);
  background-image: -webkit-linear-gradient(309deg, #9fb8ad 0%, #1fc8db 51%, #2cb5e8 75%);
  background-image: -webkit-linear-gradient(309deg, rgba(159, 184, 173, 0.2) 0%, rgba(31, 200, 219, 0.5) 51%, rgba(44, 181, 232, 0.4) 75%);
  background-image: linear-gradient(141deg, rgba(159, 184, 173, 0.2) 0%, rgba(31, 200, 219, 0.5) 51%, rgba(44, 181, 232, 0.4) 75%);
  margin: 0;
  font-family: "Nunito";
  overflow: hidden;
  color: #333745;
  font-size: 1.6rem; }

h1,
h2 {
  margin-top: 0; }

a {
  color: rgba(0, 0, 0, 0.8); }

input:focus {
  border: 1px solid #3f51b5;
  outline: unset; }

input.error {
  border: 1px solid red;
  margin-bottom: 0; }

#root {
  height: 100%; }

.flexGrow {
  flex-grow: 1; }

.snackBar-message {
  font-size: 1.2rem; }

button {
  background: 0;
  border: 1px solid #00bcd4;
  border-radius: 1.2rem;
  padding: 1.2rem 1.6rem;
  min-height: 60px;
  font-family: "Nunito";
  color: #00bcd4;
  font-weight: 600;
  font-size: 2rem;
  -webkit-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out; }
  button:focus, button:active {
    outline: none; }
  button:hover {
    cursor: pointer;
    background: #00bcd4;
    color: white; }

.iconButton {
  cursor: pointer; }

.myLocationButton {
  color: #00bcd4;
  cursor: pointer;
  position: relative;
  display: flex;
  flex-direction: column;
  font-weight: 600;
  padding: 15px 25px 15px 15px; }
  .myLocationButton:hover {
    border: 1px solid #00bcd4; }
  .myLocationButton i.fa-map-marker-alt {
    font-size: 200%; }
  .myLocationButton.loading::before {
    content: "";
    position: absolute;
    border-radius: 5px;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(255, 255, 255, 0.8); }
  .myLocationButton .textContainer {
    display: flex;
    align-items: center; }
    .myLocationButton .textContainer span {
      padding-bottom: 0.5rem;
      margin-left: 1.2rem; }

.textClickable {
  cursor: pointer; }

.autoCompleteDropDown {
  margin: 0 auto;
  position: relative; }
  .autoCompleteDropDown.opened .dropDownContent {
    display: block;
    opacity: 1; }
  .autoCompleteDropDown.opened button {
    border: 1px solid #00bcd4; }

.resultsList {
  background-color: #fafafa;
  color: #333745;
  position: absolute;
  top: 60px;
  border: 1px solid transparent;
  width: 100%;
  border-bottom-left-radius: 1.2rem;
  border-bottom-right-radius: 1.2rem;
  font-size: 2rem;
  max-height: 300px;
  overflow: hidden;
  overflow-y: auto; }
  .resultsList > div {
    padding: 1.2rem; }
    .resultsList > div:hover {
      background: rgba(0, 0, 0, 0.1);
      cursor: pointer; }
    .resultsList > div.selected {
      font-weight: bold;
      color: #00bcd4; }
    .resultsList > div.resultMessage {
      font-size: 1.6rem; }

.autoCompleteDropDown .selectButton {
  padding: 15px 15px 15px 25px;
  border: 1px solid #00bcd4;
  -webkit-transition: border 0.2s ease-in-out;
  transition: border 0.2s ease-in-out;
  top: 0;
  width: 100%;
  left: 0; }
  .autoCompleteDropDown .selectButton:hover {
    border: 1px solid #00bcd4; }
  .autoCompleteDropDown .selectButton i.fas.fa-caret-down {
    margin-left: 15px; }

.autoCompleteDropDown .dropDownContent {
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  opacity: 0;
  -webkit-transition: opacity 0.2s ease-in-out;
  transition: opacity 0.2s ease-in-out; }
  .autoCompleteDropDown .dropDownContent input.dropDownSearch {
    position: absolute;
    font-family: "Nunito";
    border-top-left-radius: 1.2rem;
    border-top-right-radius: 1.2rem;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    padding-left: 1.6rem;
    width: calc(100% - 1.4rem);
    font-weight: 600;
    font-size: 1.6rem;
    border: 0;
    color: #00bcd4; }
  .autoCompleteDropDown .dropDownContent span.clearInput {
    top: 0;
    right: 0;
    padding: 9px 20px;
    position: absolute;
    cursor: pointer;
    font-size: 3.2rem;
    color: rgba(0, 0, 0, 0.2);
    opacity: 0;
    -webkit-transition: opacity 0.2s ease-in-out;
    transition: opacity 0.2s ease-in-out; }
    .autoCompleteDropDown .dropDownContent span.clearInput.active {
      opacity: 1; }

input.inputText {
  padding-left: 1.6rem;
  margin: 1.2rem;
  margin-left: 0;
  border-radius: 1.2rem;
  border: 0;
  font-size: 2.4rem;
  font-family: "Nunito";
  min-height: 55px; }

.errorNotice {
  font-size: 12px;
  color: #ff6b5c;
  position: absolute;
  top: 100%;
  width: 100%;
  left: 0; }

.rootContainer {
  height: 100%;
  position: relative; }

.header {
  border-bottom: 1px solid #607d8b;
  color: #333745;
  display: flex;
  align-items: center;
  height: 50px;
  font-size: 3.2rem;
  justify-content: space-between;
  padding: 3.2rem;
  -webkit-transition: height 0.2s ease-in-out;
  transition: height 0.2s ease-in-out; }
  .header .iconButton {
    font-size: 3.2rem; }

.centeredContainer {
  margin: 0 auto;
  text-align: center; }

.hr {
  width: 100%;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1); }

.loadingSpinner {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%; }

.loadingSpinner img {
  width: 60px; }

.mainContainer {
  display: flex;
  flex-direction: column;
  align-items: center; }
  .mainContainer .tempInfo {
    font-size: 10rem;
    text-align: center;
    margin: 3.2rem; }
  .mainContainer .descInfo {
    display: flex;
    align-items: center;
    font-size: 3.2rem; }

@media (max-width: 45rem) {
  .header {
    font-size: 1.6rem; }
  .mainContainer .tempInfo {
    font-size: 7rem;
    margin: 1.2rem; } }

.ReactModalPortal > div {
  opacity: 0; }

.ReactModalPortal .ReactModal__Overlay {
  align-items: center;
  display: flex;
  justify-content: center;
  -webkit-transition: opacity 200ms ease-in-out;
  transition: opacity 200ms ease-in-out; }

.ReactModalPortal .ReactModal__Overlay--after-open {
  opacity: 1; }

.ReactModalPortal .ReactModal__Overlay--before-close {
  opacity: 0; }

.ReactModal__Content--after-open.modal {
  background-color: #333745;
  color: white;
  font-size: 1.6rem;
  outline: none;
  padding: 3.2rem;
  text-align: center; }

.modal__title {
  text-align: center;
  margin: 1.2rem 3.2rem;
  font-weight: bold; }

.modal__body {
  align-items: center;
  word-break: break-all;
  display: flex;
  flex-direction: row;
  padding: 1.6rem 1.6rem 0 1.6rem; }

.modal__header {
  align-items: center;
  border-bottom: 1px solid white;
  display: flex;
  justify-content: space-between;
  padding-bottom: 1.2rem; }

.modal__close {
  cursor: pointer;
  font-size: 3.2rem;
  margin-right: 1.2rem; }

.modal__left {
  border-right: 1px solid white;
  padding-right: 3.2rem;
  padding-bottom: 0; }

.modal__right {
  padding-left: 3.2rem;
  padding-top: 0; }
  .modal__right .errorNotice {
    position: relative; }

@media (max-width: 45rem) {
  .modal__body {
    flex-direction: column; }
  .modal__left {
    border-right: 0;
    border-bottom: 1px solid gray;
    padding-right: 0;
    padding-bottom: 3.2rem;
    padding-top: 1.6rem; }
  .modal__right {
    padding-left: 0;
    padding-top: 3.2rem; } }

.drawer {
  background-color: #333745;
  box-shadow: 0.1rem 0.3rem 1.2rem rgba(0, 0, 0, 0.5);
  color: white;
  font-size: 1.6rem;
  position: absolute;
  top: 24px;
  -webkit-transition: -webkit-transform 0.2s ease-in-out;
  transition: -webkit-transform 0.2s ease-in-out;
  transition: transform 0.2s ease-in-out;
  transition: transform 0.2s ease-in-out, -webkit-transform 0.2s ease-in-out;
  -webkit-transform: translateX(-110%);
          transform: translateX(-110%);
  left: 0;
  max-width: 60rem;
  padding: 1.6rem; }
  .drawer.active {
    -webkit-transform: translateX(0%);
            transform: translateX(0%); }

.drawer__header {
  display: flex;
  align-items: center;
  padding-bottom: 1.2rem;
  border-bottom: 1px solid white; }

.drawer__title {
  text-align: center;
  margin: 1.2rem 3.2rem;
  font-weight: bold; }

.drawer__close {
  cursor: pointer;
  font-size: 3.2rem;
  margin-right: 1.2rem; }

.drawer__body {
  margin: 1.2rem;
  text-align: center;
  text-transform: capitalize;
  font-size: 3.2rem; }

.drawer__body .listItem {
  cursor: pointer;
  padding: 1.2rem;
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1); }
  .drawer__body .listItem .removeButton {
    margin-left: 3.2rem; }

.mapContainer {
  position: absolute;
  bottom: 0;
  width: 100%;
  border-top: 1px solid #333745; }

